    
<template>
  <v-content class="ma-0 pa-0">
  <v-sheet color="blue lighten-5 px-4 py-9">
    <h2> Welcome back "{{ $store.state.auth.user.displayName }}"</h2>
  </v-sheet>
  <v-row>
    <v-col><app-panel title="Today" :count="total.daily.count" :amount="total.daily.amount" /></v-col>
    <v-col><app-panel title="This month" :count="total.monthly.count" :amount="total.monthly.amount" /></v-col>
  </v-row>
 <v-row>
    <v-col>
      <v-toolbar flat>
        <v-toolbar-title>
          Today  users
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn  color="green white--text" @click="$router.push('/'+($store.state.auth.user.role == 'agent_retention' ? 'retention' : 'sale') + '/new')"> <v-icon>mdi-plus</v-icon> Add new</v-btn>
      </v-toolbar>
    <v-data-table :headers="headers" :items="items"  @click:row="goTo('today', $event)"> 
     <template v-slot:item.created_on="{ item }" >
        {{ item.created_on.split('T')[0] }} 
      </template> 
      <template v-slot:item.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
      </template>
    </v-data-table></v-col>
  </v-row>
  
  <v-row>
    <v-col>
      <v-toolbar flat>
        <v-toolbar-title>
          This month  users
        </v-toolbar-title>
      </v-toolbar>
    <v-data-table :headers="mheaders" :items="mitems" @click:row="goTo('month', $event)"> 
     <template v-slot:item._id="{ item }" >
        {{ item._id.split('T')[0] }} 
      </template> 
      <template v-slot:item.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
      </template>
    </v-data-table></v-col>
  </v-row>
 
  </v-content>

</template>
<script>
import { mapActions } from 'vuex'
import AppPanel from './../components/dashboard/Panel'
import collections from './../collections'
import $ from 'jquery' 

export default {
  name: 'UserDashboard',
  components: {
    AppPanel
  },
  data () {
    return {
      headers: [],
      items: [],
      mheaders: [
        {
          text: 'Date',
          align: 'start',
          value: '_id',
          width: '30%'
        },
        { text: 'Users', value: 'count', width: '30%' },
        { text: 'Total', value: 'amount' }
      ],
      mitems: [],
      total: {
        daily: {
          amount: 0,
          count: 0
        },
        monthly: {
          amount: 0,
          count: 0
        }
      },
      dialog: {
        value: false,
        title: '',
        collection: 'sale'
      }
    }
  },
  computed: {
     
     
  },
  methods: {
    ...mapActions({
      get: 'api/get',
    }),
    goTo (type, item) {
      if (type == 'today') {
        this.$router.push('/'+(this.$store.state.auth.user.role == 'agent_retention' ? 'retention' : 'sale') + '/' + item._id)
      }
      if (type == 'month') {
        console.log(item);
        const query = {
          filterBy: [ 'user', 'created_on' ],
          filterValue:[this.$store.state.auth.user._id, item._id],
          sortBy: ['created_on'],
          sortDesc: [false]
        }
        // console.log(query, $);
        this.$router.push('/' + (this.$store.state.auth.user.role == 'agent_retention' ? 'retention' : 'sale') + '?' + $.param(query))

      }
       
    },
    writeAccess (collection) {
      const access = this.access(collection)
      if (access.length > 0)
        return access[0].write

      return false 
    },
    access (collection) {
        if (this.$store.state.auth.user.rights) {
          return this.$store.state.auth.user.rights.filter(e => {
              return e.value ==  collection  
          })
        } 
        return []
      },
    openDialog (collection) {

      this.dialog.title = collection
      this.dialog.collection = collection
      this.dialog.value = true
    },
    newSave () {
      this.dialog.value = false
    }
  },
  async created () {
    const totalDaily = await this.get({ collection: 'dashboard/user_daily' }) 
    const totalMonthly = await this.get({ collection: 'dashboard/user_monthly' }) 
    
    if (totalDaily && totalDaily.data ) {
      this.total.daily.amount = totalDaily.data.length ? totalDaily.data[0].amount : 0
      this.total.daily.count = totalDaily.data.length ? totalDaily.data[0].count : 0
    }
    
    if (totalMonthly && totalMonthly.data ) {
      this.total.monthly.amount = totalMonthly.data.length ? totalMonthly.data[0].amount : 0
      this.total.monthly.count = totalMonthly.data.length ? totalMonthly.data[0].count : 0
    }
    
    const { role } = this.$store.state.auth.user
    const collection = collections.get(role == 'agent_retention' ? 'retention' : 'sale')
    this.headers = Object.assign([],collection.table)
    this.headers = this.headers.filter(e => ["actions", "business_unit", "user.username"].indexOf(e.value) === -1)
    console.log(this.headers, collection.table);

     let d = new Date();
    d.setHours(0, 0, 0, 0);
    const result = await this.get({ collection: role == 'agent_retention' ? 'retention' : 'sale', query: {
      query: {user: {type: "normal", value: this.$store.state.auth.user._id}, created_on: {type: "gte", value:d}}, 
      limit: 10
    } })
    if (result && result.data)
      this.items = result.data.docs

    const mresult = await this.get({ collection: 'dashboard/user_monthly_deals'})
    if (mresult && mresult.data)
      this.mitems = mresult.data 
 



  }
}
</script>